
html {
  -webkit-text-size-adjust: 100%;
  font-size: 625%;
}
@media screen and (max-width: 319px) {
html {
    font-size: 80px;
}
}
@media screen and (min-width: 320px) and (max-width: 359px) {
html {
    font-size: 80px;
}
}
@media screen and (min-width: 360px) and (max-width: 399px) {
html {
    font-size: 100px;
}
}
@media screen and (min-width: 400px) and (max-width: 413px) {
html {
    font-size: 100px;
}
}
@media screen and (min-width: 414px) and (max-width: 639px) {
html {
    font-size: 100px;
}
}
@media screen and (min-width: 640px) and (max-width: 1079px) {
html {
    font-size: 150px;
}
}
@media screen and (min-height: 1800px) {
html {
    font-size: 280px;
}
}
@media screen and (min-width: 1080px) {
html {
    font-size: 280px;
}
}
body,
div,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
button,
textarea,
p,
blockquote,
th,
td,
hr {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
}
body {
  font-family: "Helvetica Neue", Helvetica, STHeiTi, Arial, "Microsoft YaHei";
  font-size: 0.14rem;
  line-height: 1.5;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  color: #333;
  background: #e1e1e1;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
svg:not(:root) {
  overflow: hidden;
}
a {
  color: #333;
  background: transparent;
  text-decoration: none;
  -webkit-tap-highlight-color: transparent;
}
a:active {
  outline: 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
mark {
  background: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
hr {
  box-sizing: content-box;
  height: 0;
}
pre {
  overflow: auto;
  white-space: pre;
  white-space: pre-wrap;
  word-wrap: break-word;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
button {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
input {
  line-height: normal;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
input {
  outline: none;
  border: none;
}
textarea {
  overflow: auto;
  resize: vertical;
  border: none;
  resize: none;
  outline: none;
}
optgroup {
  font-weight: bold;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
}
ul,
ol {
  list-style: none outside none;
}
h1,
h2,
h3 {
  line-height: 2;
  font-weight: normal;
}
h1 {
  font-size: 18px;
}
h2 {
  font-size: 16px;
}
h3 {
  font-size: 14px;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  color: #ccc;
}
* {
  background-color: white;
}
.top {
  width: 100%;
  height: 60px;
  box-shadow: 0px 1px 2px grey;
}
.top .top-li {
  display: inline-block;
  float: left;
  height: 60px;
  font-size: 25px;
  line-height: 60px;
}
.top .top-li a {
  padding: 0px 30px;
}
.top .top-li:hover {
  background-color: darkgrey;
}
.main {
  width: 70%;
  height: 500px;
  margin: 0px auto;
  margin-top: 30px;
}
.main table {
  width: 100%;
  font-size: 15px;
  border: solid #ADADAD;
  border-width: 1px 0px 0px 1px;
}
.main table th {
  line-height: 40px;
  background-color: ;
  border: solid #ADADAD;
  border-width: 0px 1px 1px 0px;
}
.main table td {
  line-height: 30px;
  width: 10%;
  text-align: center;
  border: solid #ADADAD;
  border-width: 0px 1px 1px 0px;
}
.main table span {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-size: 20px 20px;
  line-height: 20px;
}
.title {
  background: url("assets/normal.svg");
}
.num0up,
.num1up,
.num2up,
.num3up,
.num4up,
.num5up {
  background-image: url("assets/up.svg");
}
.num0down,
.num1down,
.num2down,
.num3down,
.num4down,
.num5down {
  background-image: url("assets/down.svg");
}



